@import url(//fonts.googleapis.com/css?family=Lora);
@import url(//fonts.googleapis.com/css?family=Varta);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body {
  background-color: #f7f5f0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  position: relative;
}

.app-container {
  min-height: 100vh;
}

.about-hello {
  margin-top: 50px;
}

.headshot {
  margin-top: 0px;
  padding-right: 25px;
}

.headers {
  background-color: #f7f5f0;
  display: flex;
  font-family: "Lora";
  text-decoration: none;
  padding-top: 40px;
  justify-content: center;
  z-index: 1;
}

.left-headers {
  display: flex;
  justify-content: flex-start;
}

.right-headers {
  display: flex;
  text-align: end;
  font-family: "Lora";
  text-decoration: none;
}

.header {
  text-decoration: none;
  color: black;
  margin-left: 50px;
}

.filler {
  display: flex;
  flex: 1 1;
}

.margin {
  margin-left: 23%;
  margin-right: 23%;
}

.name {
  text-decoration: none;
  color: black;
}

.about-body {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 80%;
  width: 100%;
  margin-top: 100px;
}

.about-middle {
  display: table-cell;
  vertical-align: middle;
  font-family: "Lora";
}

.about-inner {
  margin-left: auto;
  margin-right: auto;
  width: 54%;
  /* Whatever width you want */
}

.about-header {
  font-size: 4em;
  display: flex;
  flex-direction: row;
}

.about-icons {
  display: flex;
  flex-direction: row;
}

.about-linkedin {
  margin-left: 20px;
}

.about-mark {
  background-color: #ebe5f5;
  background: linear-gradient(to top, #ebe5f5 60%, transparent 40%);
  display: inline-flex;
  margin-left: 44px;
  width: 140px;
  justify-content: flex-end;
}

.about-link {
  background-color: #ebe5f5;
  background: linear-gradient(to top, #ebe5f5 5%, #f7f5f0 95%);
}

.project-link {
  background-color: #ebe5f5;
  background: linear-gradient(to top, #ebe5f5 5%, white 95%);
}

.project-link:hover {
  background-color: #ebe5f5;
  background: linear-gradient(to top, #ebe5f5 100%, #f7f5f0 0%);
}

.about-link:hover {
  background-color: #ebe5f5;
  background: linear-gradient(to top, #ebe5f5 100%, #f7f5f0 0%);
}

.about-indent {
  margin-left: 25px;
}

span {
  background-position: right bottom;
}

p {
  margin-bottom: 0.1%;
}

.about-paragraph {
  font-family: "Varta";
  font-size: 18px;
  padding-bottom: 30px;
}

.about-blueprint {
  text-decoration: none;
}

span {
  display: inline-block;
}

.about-subheader {
  width: 100%;
  font-size: 1.5em;
}

.general-center {
  margin-top: 10%;
}

.general-header {
  font-family: "Lora";
  font-size: 2em;
  font-weight: 280;
}

.general-subheader {
  font-family: "Lora";
}

a {
  text-decoration: none;
  color: black;
}

mark {
  background-color: #ebe5f5;
}

mark:hover {
  color: #093878;
}

.project-background {
  width: 100%;
  height: 10%;
  background-color: white;
  margin-top: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 15px rgb(0 0 0 / 25%);
  transition: transform 0.2s;
}

.project-background:hover {
  transform: scale(1.01);
}

.project-header {
  font-family: "Lora";
  font-size: 18px;
}

.project-left {
  width: 40%;
}

.project-padding {
  padding: 20px;
}

.project-pic {
  display: table;
  justify-content: start;
  margin-top: 10px;
  width: 100%;
}

.project-pic-middle {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  width: 100%;
}

.project-pic-wrapper {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.project-desc {
  font-family: "Varta";
  font-size: 14px;
  margin-top: 18px;
}

.project-rightmargin {
  margin-right: 3px;
}

.project-video {
  width: 32vw;
  height: 32vh;
}

.project-list {
  padding-bottom: 50px;
}

.project-picsize {
  max-width: 70%;
  width: auto;
  height: auto;
}

.project-pic-only {
  margin-right: 0px;
}

.project-techstack {
  font-style: italic;
}

.work-term {
  margin-left: 25px;
  font-style: italic;
  font-size: 0.9em;
}

.workexpand-picsize {
  max-width: 100%;
  width: auto;
  height: auto;
}

.workexpand-pic {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  max-width: 100%;
}

.work-button {
  font-family: Varta;
  background-color: white;
  border-width: 1px;
  border-radius: 3px;
  border-color: grey;
  border-style: solid;
  width: 70%;
  font-size: 1.9vh;
  transition: 0.3s;
}

.work-button:hover {
  font-family: Varta;
  border-width: 1px;
  border-radius: 3px;
  border-color: grey;
  border-style: solid;
  cursor: pointer;
  background-color: #ebe5f5;
}

.work-card-desc {
  margin-left: 2.5%;
  display: flex;
  flex-direction: column;
}

.work-card-desc-text {
  margin-top: 20%;
  font-family: Varta;
  margin-bottom: 15px;
  font-size: 3vh;
}

.learnmore {
  margin-top: 15%;
  vertical-align: top;
  font-family: Varta;
  display: flex;
  flex-direction: row;
}

.work-exp-back {
  font-family: "Lora";
}

